export default {
	data() {
		return {
			entity: {},
			ctx:this.$ctx,
			options:{},
			status:{
				0:'待付款',
				1: '已付款',
				30: '已完成',
				40: '已取消',
				2: '售后',
				6: '售后',
				11: '售后',
				25: '售后',
				26: '售后',
				50: '退款成功',
			}
		}
	},
	onLoad(options) {
		this.options=options
		this.getData()
	},
	methods: {
		getData() {
		
			this.$getJson("/api/v3/order/info.jsp", {
				orderId: this.options.id,
				orderNo: this.options.orderNo
			}, "POST", res => {
				console.log(res)
				this.entity = res.data

			})
		},
		pay(item) {
			uni.navigateTo({
				url: '/pages/money/pay?money=' + item.shouldPayAmountShow + '&sn=' + item.sn
			});
		},
		//取消订单
		cancelOrder(item) {
			uni.showModal({
				content: '是否取消订单？',
				success: (e) => {
					if (e.confirm) {
						this.$getJson("/jsp/api/order/doCancel.jsp", {
							id: item.id,
						}, "POST", res => {
							uni.hideLoading(); //正在加载
							if (res.success == true || res.success == 'true') {
								this.getData()
							}
						})
					}
				}
			})
		
		},
		copyText: function(e) {
			console.log(e)
			wx.setClipboardData({
				data: e.currentTarget.dataset.text,
				success: function(res) {
					wx.getClipboardData({
						success: function(res) {
							wx.showToast({
								title: '复制成功'
							})
						}
					})
				}
			})
		},

	}
}
